/* Navbar.css */

header {
  padding: 14px 14px;
  position: fixed;
  top: 30px;
  width: 100%;
  background: white;
  display: grid;
  z-index: 12;
  place-items: center;
  transition: top 0.3s ease;
}

.Navbar-con.scrolled {
  top: 0;
  transition: top 0.3s ease;
}

.navbar.scrolled {
  top: 0;
  transition: top 0.3s ease;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-width: 100rem;
  transition: top 0.3s ease; /* Added transition here as well */
}



  .links{
    display: flex;
  }
  
  ul {
    list-style: none;
    display: flex;
  }

  
  li {
    cursor: pointer;
    font-size: 13px;
    margin-left: 3em;
    margin-right: 3em;
    transition: 1sm linear;
  }


  li:hover{
    color:  #d2b7b7 ;
  }


  .cart-container{
    position: relative;
    left: 15px;

  }
  
  .cart {
    list-style: none;
    width: 0px;
    font-size: 20px;
  }

  .cart:hover{
    color: black;
  }

  .cart-container span{
    position: absolute;
    top: -9px;
    right: 31px;
    font-size: 11px;
   }
  
  .logo {
    font-size: 1.4em;
    font-weight: bold;
    letter-spacing: 1px;
  }

  .logo h1{
    font-family: 'Sevillana', serif;
    font-weight: 700;
    letter-spacing: initial;
    font-size: 30px;
  }
  
  .dropdowncart{
    display: flex;
    align-items: center;
  }

  .dropdown {
    display: none;
  }

  .dropdown button{
    background: none;
    border: none;
    vertical-align: inherit;
    cursor: pointer;
    font-size: 20px;
    }